import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    frutas: [
      {nombre: 'Ciruelas', cantidad: 10},
      {nombre: 'Uvas', cantidad: 10},
      {nombre: 'Kiwis', cantidad: 10}

    ]
  },
  getters: {
  },
  mutations: {
    aumentar(state,index){
      state.frutas[index].cantidad++
    },
    reset (state){
      state.frutas.forEach(elemento => {
        elemento.cantidad = 0
      })
    },


  },
  actions: {
  },
  modules: {
  }
})
