<template>
    <div>
        <ul class="list-group">
            <li v-for="(item,index) of frutas" :key="'fruta'+index"
            @click="aumentar(index)"
            class="list-group-item d-flex justify-content-between align-items-center">
              {{item.nombre}}
              <span class="badge badge-warning badge-pill">{{item.cantidad}}</span>
            </li>
          </ul>
          <button class="btn btn-success btn-block" @click="reset">RESET</button>
    </div>
</template>

<script>

    import {mapState, mapMutations} from 'vuex';

    export default {
        name: 'Lista',
        computed: {
            ...mapState (['frutas']),
            

        },
        methods: {
            ...mapMutations (['aumentar' , 'reset'])
        }

        
    }
</script>